<template>
  <div id="main">
    <!---->
    <Header HeaderTitle="" HomeTitle=""></Header>
    <!---->
    <!---->
    <section class="content">
      <div class="con-items">
        <div class="con-item con-head">
          <span class="con-item-title"></span>
          <img :src="Info.headimg" class="con-item-head" alt="">
        </div>
        <div class="con-item" @click="EditName">
          <span class="con-item-title"></span>
          <input type="text" class="con-item-val" placeholder="" :value="Info.name" disabled="true">
        </div>
        <div class="con-item" @click="ChoseSex">
          <span class="con-item-title"></span>
          <input type="text" class="con-item-val" placeholder="" disabled="true" :value="Info.sex">
          <img src="../../img/Info-right.png" class="con-item-right" alt="">
        </div>
        <div class="con-item btn" data-options='{"type":"date","beginYear":1900,"endYear":2086}' @click="BirthdayHit($event,{'type':'date','beginYear':1900,'endYear':2086})">
          <span class="con-item-title"></span>
          <input type="text" class="con-item-val birthday" placeholder="" disabled="true">
          <img src="../../img/Info-right.png" class="con-item-right" alt="">
        </div>
        <div class="con-item" @click="ChoseCity">
          <span class="con-item-title"></span>
          <input type="text" class="con-item-val" placeholder="" disabled="true" :value="resultCity">
          <img src="../../img/Info-right.png" class="con-item-right" alt="">
        </div>
      </div>
      <div class="con-border"></div>
      <div class="con-items">
        <router-link to="/VehicleCert">
          <div class="con-item">
            <span class="con-item-title"></span>
            <img src="../../img/Info-right.png" class="con-item-right" alt="">
          </div>
        </router-link>
        <div class="con-item">
          <span class="con-item-title"></span>
          <img src="../../img/Info-right.png" class="con-item-right" alt="">
        </div>
      </div>
    </section>
    <!---->
    <!---->
    <div class="shade" v-if="ShowShade" @click="CloseShade"></div>
    <!---->
    <div class="CPM-EditName" v-if="ShowEditName">
      <div class="EditName-title"></div>
      <div class="EditName-content">
        <div class="EditName-con-nameVal">
          <div class="EditName-con-name" contenteditable="true" placeholder="Info.name">{{Info.name}}</div>
          <img class="EditName-clear-logo" src="../../img/MemberCenter/personal/cancel.png" alt="" @click="ClearName">
        </div>
      </div>
      <div class="EditName-bts">
        <span class="EditName-btn EditName-btn-cancel" @click="CloseShade"></span>
        <span class="EditName-btn EditName-btn-sure" @click="EditNameSure"></span>
      </div>
    </div>
    <!---->
    <div class="CPM-Sex" v-if="ShowChoseSex">
      <div class="SexBox-btns">
        <span class="SexBox-cancel" @click="CloseShade"></span>
        <span class="SexBox-sure" @click="ChoseSexSure"></span>
      </div>
      <ul class="SexBox-con">
        <li class="SexBox-con-item" :class="sexnav==index?'SexBox-con-itemAct':''" v-for="(sex,index) in sexList" :data-id="index" :key="index" @click="ChoseSexVal($event,index)">
          {{sex.text}}
        </li>
      </ul>
    </div>
    <!---->
    <CPMCity v-if="ShowChoseCity" @getLinkage="GetLink"></CPMCity>
  </div>
</template>

<script>
  import MyLoading from '../../../static/Loading/Loading'
  import Header from './Header'
  import {submitBir} from '../../js/birthday';
  import CPMCity from './CPMCity'

    export default {
      name: "Personal",
      data(){
        return{
          Info:{headimg:require('../../img/head.jpg'),name:'',phone:'18501297827',sex:''},
          ShowShade:false,//
          ShowEditName:false,//
          ShowChoseSex:false,//
          ShowChoseCity:false,
          sexList: [
            { text: '' },
            { text: '' },
          ],
          sexnav:0,
          resultCity:'',//
          idCity:''//id
        }
      },
      methods:{
        // 
        EditName(){
          this.ShowShade=true;
          this.ShowEditName=true;
        },
        //
        ChoseSex(){
          this.ShowShade=true;
          this.ShowChoseSex=true;
        },
        //
        BirthdayHit(e,day){
          submitBir(e,day)
        },
        //
        ChoseCity(){
          this.ShowShade=true;
          this.ShowChoseCity=true;
        },
        // 
        EditNameSure(){
          var name = $(".EditName-con-name").text();//name
          // console.log(name);
          this.$MyToast({
            type:'success',
            title:'',
          })
          this.CloseShade();//
          /*let data={"name":name};
          this.$http.post("/api/",data).then((res)=>{
            if(res.body.status==1){
              this.CloseShade();//
            }
            else{
              this.$MyToast({
                type:'fail',
                title:res.body.msg
              })
            }
          })*/
          this.Info.name=name//
        },
        //
        ClearName(){
          this.Info.name = '';
        },
        // 
        ChoseSexVal(e,index){
          this.sexnav = index;//id
          var el = e.target;// e.currentTarget
          var SexVal = el.innerText;//
          // console.log(SexVal);
          this.Info.sex = SexVal;
        },
        // 
        ChoseSexSure(){
          var sex = this.Info.sex;
          console.log(sex);
          this.CloseShade();
        },
        // birthday.js
        // 
        GetLink(msg){
          console.log(msg);
          this.resultCity=msg.name;
          this.idCity=msg.id;
          this.CloseShade();
        },
        // 
        CloseShade(){
          this.ShowShade=false;
          this.ShowEditName=false;
          this.ShowChoseSex=false;
          this.ShowChoseCity=false;
        }
      },
      components:{
        Header,
        CPMCity,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: white;
  }
  /**/
  .content{
    margin-top: 0.88rem;
    border-top: 0.2rem solid #F7F7F7;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-items{
    padding: 0 4%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }
  .con-border{
    width: 100%;
    height: 0.1rem;
    background-color: #F7F7F7;
  }
  .con-item{
    width: 100%;
    height: 0.99rem;
    border-bottom: 1px solid #F2F2F2;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    -webkit-align-items: center;
    font-size: 0.32rem;
    color: #333333;
    font-weight: 500;
  }
  .con-item:last-child{
    border-bottom: 0px;
  }
  .con-item-val{
    flex: 1;
    text-align: right;
  }
  .con-item-right{
    width: 0.14rem;
    height: 0.26rem;
    margin-left: 0.1rem;
  }
  .con-head{
    height: 1.8rem;
  }
  .con-item-head{
    width: 1.4rem;
    height: 1.4rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
  }
  /**/
  .shade{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000000;
    opacity: 0.2;
    z-index: 100;
  }
  /**/
  .CPM-EditName{
    width: 92%;
    height: 4.4rem;
    background-color: #FFFFFF;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -2.2rem;
    margin-left: -46%;
    z-index: 111;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    font-size: 0.32rem;
    color: #333333;
    font-weight: 500;
  }
  .EditName-title{
    width: 100%;
    height: 0.98rem;
    border-bottom: 2px solid #F7F7F7;
    line-height: 0.98rem;
  }
  .EditName-content{
    width: 86.96%;
    height: 2.38rem;
    margin: 0 6.52%;
  }
  .EditName-con-nameVal{
    width: 100%;
    height: 0.6rem;
    border-bottom: 2px solid #999999;
    margin-top: 14.09%;
    padding: 0 1.45%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .EditName-con-name{
    width: 80%;
    height: 100%;
    text-align: left;
    line-height: 0.58rem;
    border: none;
  }
  .EditName-con-name:focus:before{
    content:none;
  }
  .EditName-con-name:focus{
    border: none;outline:none;
  }
  .EditName-clear-logo{
    width: 0.3rem;
    height: 0.3rem;
  }
  .EditName-bts{
    width: 100%;
    height: 1rem;
    display: flex;
    padding: 0 11.47%;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    -webkit-align-items: center;
  }
  .EditName-btn-cancel{
    color: #999999;
  }
  .EditName-btn-sure{
    color: #0079FF;
  }
  /**/
  .CPM-Sex{
    width: 100%;
    height: 4.5rem;
    position: fixed;
    /*bottom: -6.5rem;*/
    bottom: 0;
    left: 0;
    z-index: 111;
    background-color: white;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
    font-size: 0.32rem;
    font-weight: 500;
    line-height: 1rem;
  }
  .SexBox-btns{
    width: 100%;
    height: 1rem;
    padding: 0 5.87%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .SexBox-cancel{
    color: #999999;
  }
  .SexBox-sure{
    color: #0079FF;
  }
  .SexBox-con{
    width: 100%;
    margin-top: 0.5rem;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .SexBox-con-item{
    width: 100%;
    height: 1rem;
    border-top: 2px solid #F7F7F7;
  }
  .SexBox-con-item{
    color: #B3B3B3;
  }
  .SexBox-con-itemAct{
    color: #010101;
  }
</style>



