<template>
  <div id="main">
    <!---->
    <Header HeaderTitle="" HomeTitle=""></Header>
    <!---->
    <div class="content">
      <!---->
      <div class="StepOne" v-if="ShowStepOne">
        <img class="StepOne-PhoneLogo" src="../../img/MemberCenter/ChangePhonelogo.png" alt="">
        <div class="StepOne-phone">{{OnePhone}}</div>
        <div class="StepOne-code">
          <input class="StepOne-codeVal" type="number" placeholder="" v-model="OneCode">
          <div class="codeBox">
            <span class="sendCode" id="btnSendCode1" @click="SendCodeOne">{{GetSendCodeHtml()}}</span>
          </div>
        </div>
        <div class="StepOne-btn" @click="NextStep"></div>
      </div>
      <!---->
      <!---->
      <div class="StepTwo" v-if="ShowStepTwo">
        <div class="StepTwo-item">
          <input type="tel" placeholder="" v-model="TwoPhone">
          <div class="codeBox">
            <span class="sendCode" id="btnSendCode2" @click="SendCodeTwo">{{GetSendCodeHtml()}}</span>
          </div>
        </div>
        <div class="StepTwo-item StepTwo-codebox">
          <div class="StepTwo-item-title"></div>
          <input class="StepTwo-item-code" type="number" placeholder="" v-model="TwoCode">
        </div>
        <div class="StepTwo-btn" @click="Submit"></div>
      </div>
      <!---->
    </div>
  </div>
</template>

<script>
  import Header from './Header'
  import $ from 'jquery'
  import {sendMessage} from "../../js/sendCode";
    export default {
      name: "AccountChangePhone",
      data(){
        return{
          ShowStepOne:true,//
          ShowStepTwo:false,//
          OnePhone:'18501297827',//
          OneCode:'',//
          TwoPhone:'',//
          TwoCode:'',//
          count:false,//
          countNum:1//
        }
      },
      methods:{
        // 
        SendCodeOne(){
          if (this.count > 0) {//
            return false;
          }
          if(this.countNum > 5){//5
            this.$MyAlert({
              type:'prompt',
              title:'1',
              btntitle:'',
            });
            return false;
          }
          this.RemainTime();
        },
        // 
        NextStep(){
          var OneCode=this.OneCode;
          // console.log(OneCode);
          if(OneCode){
            this.ShowStepOne=false;
            this.ShowStepTwo=true;
            this.count = false;//
            this.countNum=1;//
            return;
          }
          if($('#MyToast').length && $('#MyToast').length>0){
            return false;
          }
          this.$MyToast({
            type:'prompt',
            title:'',
          })
        },
        // 
        SendCodeTwo(){
          if (this.count > 0) {//
            return false;
          }
          if(this.countNum > 5){//5
            this.$MyAlert({
              type:'prompt',
              title:'1',
              btntitle:'',
            });
            return false;
          }
          var phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/;//
          var phone = $.trim(this.TwoPhone);
          if (!phoneReg.test(phone)) {
            if($('#MyToast').length && $('#MyToast').length>0){
              return false;
            }
            this.$MyToast({
              type:'prompt',
              title:'',
            });
            return false;
          }
          this.RemainTime();
        },
        //
        Submit(){
          var TwoPhone = this.TwoPhone;
          var TwoCode = this.TwoCode;
          // console.log(TwoPhone,TwoCode);
          if($('#MyToast').length && $('#MyToast').length>0){
            return;
          };
          if(!TwoPhone){
            this.$MyToast({
              type:'prompt',
              title:'',
            });
            return;
          }
          var phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/;//
          var phone = $.trim(this.TwoPhone);
          if (!phoneReg.test(phone)) {
            this.$MyToast({
              type:'prompt',
              title:'',
            });
            return;
          }
          if(!TwoCode){
            this.$MyToast({
              type:'prompt',
              title:'',
            });
            return;
          }
        },
        // 60
        RemainTime(){
          this.countNum++;
          this.count = 5;//
          var that = this;
          var timer_interval = setInterval(function(){
            if(that.count > 0){
              that.count -- ;
            }else{
              clearInterval(timer_interval);
            }
          },1000);
          //ajax
        },
        //html
        GetSendCodeHtml(){
          if(this.count>0){
            return this.count+'s';
          }
          if(this.count=== 0){
            return '';
          }
          if(this.count===false){
            return '';
          }
        },
      },
      components:{
        Header,
      }
    }
</script>

<style scoped>
  #main{
    width: 100%;
    min-height: 100%;
    position: absolute;
    background-color: white;
  }
  .content{
    width: 100%;
    margin-top: 0.88rem;
    border-top: 0.2rem solid #F7F7F7;
  }
  /**/
  .StepOne{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .StepOne-PhoneLogo{
    width: 2.4rem;
    height: 2rem;
    margin-top: 0.5rem;
  }
  .StepOne-phone{
    width: 100%;
    font-size: 0.3rem;
    color: #333333;
    font-weight: 500;
    text-align: center;
    line-height: 0.3rem;
    margin-top: 0.53rem;
  }
  .StepOne-code{
    width: 100%;
    height: 0.98rem;
    padding: 0 4% 0 5.2%;
    border-bottom: 0.02rem solid #F7F7F7;
    margin-top: 0.24rem;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .StepOne-codeVal{
    flex: 1;
    font-size: 0.28rem;
    line-height: 0.3rem;
    border: none;
  }
  .codeBox{
    width: 25%;
    height: 0.6rem;
    border: 0.02rem solid #448CFF;
    border-radius: 0.04rem;
    -webkit-border-radius: 0.04rem;
    -moz-border-radius: 0.04rem;
    font-size: 0.24rem;
    color: #0079FF;
    text-align: center;
    line-height: 0.56rem;
  }
  .StepOne-btn,.StepTwo-btn{
    width: 53.33%;
    height: 0.9rem;
    background-color: #399EFA;
    font-size: 0.32rem;
    color: #FFFFFF;
    text-align: center;
    line-height: 0.9rem;
    border-radius: 0.04rem;
    -webkit-border-radius: 0.04rem;
    -moz-border-radius: 0.04rem;
    margin-top: 2.5rem;
  }
  /**/
  .StepTwo{
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    -webkit-align-items: center;
  }
  .StepTwo-item{
    width: 100%;
    height: 1rem;
    border-bottom: 0.2rem solid #F7F7F7;
    padding: 0 4% 0 5.47%;
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
    align-items: center;
    -webkit-align-items: center;
    justify-content: space-between;
    -webkit-justify-content: space-between;
  }
  .StepTwo-codebox{
    border-bottom: 0.02rem solid #F7F7F7;
  }
  .StepTwo-item input{
    flex:1;
    font-size: 0.32rem;
    line-height: 1rem;
    border:none;
  }
  .StepTwo-item-title{
    font-size: 0.32rem;
    color: #333333;
    line-height: 1rem;
  }
  .StepTwo-item-code{
    text-align: right;
  }
</style>



