var sendMessage = function () {
  sendMessage1();
}
export {
  sendMessage
}

var phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/;//
var count = 60; //1
var InterValObj1; //timer
var curCount1;//
/**/
function sendMessage1() {
  curCount1 = count;
  var phone = $.trim(this.OnePhone);
  console.log(phone);
  if (!phoneReg.test(phone)) {
    // $.myToast("");
    this.$MyToast({
      type:'prompt',
      title:'',
    })
    return false;
  }
  else{
    //button
    $("#btnSendCode1").css("pointer-events", "none"); //
    // $("#btnSendCode1").addClass("sendBtn");//
    $("#btnSendCode1").html( + curCount1 + "S");//
    InterValObj1 = window.setInterval(SetRemainTime1, 1000); //1
    //
    /*$.ajax({
      url:"",
      method:"post",
      data:"phone",
      dataType:"json",
      success:function () {

      },
      error:function () {
        $.myToast("");
      }
    })*/
  }
}
function SetRemainTime1() {
  if (curCount1 == 0) {
    window.clearInterval(InterValObj1);//
    // $("#btnSendCode1").removeClass("sendBtn");//
    $("#btnSendCode1").css("pointer-events", "auto");//
    $("#btnSendCode1").html("");
  }
  else {
    curCount1--;
    $("#btnSendCode1").html( + curCount1 + "S");
  }
}



